@-webkit-keyframes arr {
  0%{-webkit-transform: translateX(-20px);transform: translateX(-20px);color: #000}
  /* 25%{color: #000;} */
  50%{-webkit-transform: translateX(-150px);transform: translateX(-150px);color: #000}
